import React from 'react'

import { ga } from '../utils'

// TODO          

// TODO format viewsCount & favoritesCount

// TODO   "-"     

const PostInfoPanel = ({ id, author, viewsCount, favoritesCount, isTeaser }) => {

  const postsAddToFavorites = (event) => {
    // TODO posts_add_to_favorite}(this, '2', id)
    alert('postsAddToFavorite')
  }

  const postsVotePlus = (event) => {
    // TODO posts_vote(this, id, '2', 1)
    alert('postsVotePlus')
    ga('voting_location', 'plus', window.location.href)
  }

  const postsVoteMinus = (event) => {
    // TODO posts_vote(this, id, '2', -1)
    alert('postsVoteMinus')
    ga('voting_location', 'minus', window.location.href)
  }

  const postsVoteZero = (event) => {
    // TODO posts_vote(this, id, '2', 0)
    alert('postsVoteZero')
    ga('voting_location', 'zero', window.location.href)
  }

  return (
    <div className="infopanel_wrapper">{/*  js-user_ */}
      <ul className={`postinfo-panel${isTeaser ? '' : ' postinfo-panel_post'}`} id={`infopanel_post_${id}`}>
        <li className="postinfo-panel__item">
          <div className="voting-wjt voting-wjt_infopanel js-voting">
            <button type="button" className="voting-wjt__button voting-wjt__button_plus js-plus" title="" onClick={postsVotePlus}>
              <span></span>
            </button>
            <div className="voting-wjt__counter js-mark">
              <button type="button" className="voting-wjt__button voting-wjt__button_score js-score" title=" " onClick={postsVoteZero}></button>
            </div>
            <button type="button" className="voting-wjt__button voting-wjt__button_minus js-minus" title=" " onClick={postsVoteMinus}>
              <span></span>
            </button>
          </div>
        </li>
        <li className="postinfo-panel__item">
          <div className="views-count_post" title=" ">{viewsCount}</div>
        </li>
        <li className="postinfo-panel__item">
          <div className="favorite-wjt favorite">
            <button type="button" className="favorite-wjt__button add" title="  " onClick={postsAddToFavorites}>
              <span> </span>
            </button>
            <span className="favorite-wjt__counter js-favs_count" title=" ,    ">{favoritesCount}</span>
          </div>
        </li>
        {isTeaser &&
          <li className="postinfo-panel__item post-author">
            <a title=" " className="post-author__link" href={`/#/users/${author.nick}/`}>
              {!!author.avatar ? <img src={author.avatar} className="post-author__pic" alt={author.name} /> : <span className="post-author__pic_default user-pic_blue"></span>}@{author.nick}
            </a>
          </li>
        }
        {isTeaser &&
          <li className="postinfo-panel__item postinfo-panel__item_comments">
            <div className="post-comments">
              <a href={`/#/post/${id}/#comments`} className="post-comments__link" title="">
                <span className="post-comments_all"></span>
              </a>
            </div>
          </li>
        }
        <li className="postinfo-panel__item postinfo-panel__item_align-right">
          {!isTeaser &&
            <ul className="post-share">{/* TODO    ,      */}
              <li className="post-share__item">
                <a href="#" className="post-share__item-link twitter" title="   Twitter" />
              </li>
              <li className="post-share__item">
                <a href="#" className="post-share__item-link vkontakte" title="   " />
              </li>
              <li className="post-share__item">
                <a href="#" className="post-share__item-link facebook" title="   Facebook" />
              </li>
              <li className="post-share__item">
                <a href="#" target="_blank" className="post-share__item-link pocket" title="   Pocket" />
              </li>
            </ul>
          }
        </li>
      </ul>
    </div>
  )
}

export default PostInfoPanel



