import React from 'react'

import { ga, formatDateTime } from '../utils'

// TODO  flow  props,   (  )
// TODO  "    "    
// TODO   : Tutorial, Recovery mode,  , 
// TODO     

const PostHeader = ({ isTeaser, id, title, published, flow, hubs }) => (
  <div className="post__header">
    <span className="post__time_published">{formatDateTime(published)}</span>
    <h2 className="post__title">
      { isTeaser ? <div><a href={`/#/flows/${flow.id}/`} className="post__flow" onClick={ga.bind(void 0, 'flow', 'feed page', flow.name)}>{flow.name}</a><span className="post__title-arrow"> </span><a href={`/#/post/${id}/`} className="post__title_link">{title}</a></div> : <span>{title}</span> }
    </h2>
    <div className="hubs">{/* TODO       CSS */}
      {/* TODO     <div>   .profiled_hub */}
      {/* hub.isProfiled && <span className="profiled_hub" title=" ">*</span> */}
      {hubs.map(hub =>
        <a key={hub.id} href={`/#/hub/${hub.id}/`} className={`hub${hub.isSubscribed ? ' subscribed' : ''}`} title={hub.isSubscribed ? '    ' : '     '} onClick={ga.bind(void 0, 'hub', 'feed page', hub.name)}>{hub.name}</a>
      )}
    </div>
  </div>
)

export default PostHeader



