import React from 'react'
import { render } from 'react-dom'
import { Provider } from 'react-redux'
import { createStore, applyMiddleware } from 'redux'
import { composeWithDevTools } from 'redux-devtools-extension'
import thunk from 'redux-thunk'
import { Router, Route, IndexRoute, Redirect, hashHistory } from 'react-router'
import { syncHistoryWithStore } from 'react-router-redux'
import createLogger from 'redux-logger'

import reducer from './reducers'

import App from './components/App'
import PostTeaserList from './components/PostTeaserList'
import PostFull from './components/PostFull'
import Feedback from './components/Feedback'
import TopicAdd from './components/TopicAdd'
import NotFound from './components/NotFound'
import InfoHelp from './components/InfoHelp'
import InfoAbout from './components/InfoAbout'
import InfoHelpRules from './components/InfoHelpRules'
import InfoHelpOther from './components/InfoHelpOther'
import InfoHelpRegistration from './components/InfoHelpRegistration'
import InfoHelpHabracentre from './components/InfoHelpHabracentre'
import InfoHelpKarma from './components/InfoHelpKarma'
import InfoHelpSettings from './components/InfoHelpSettings'
import InfoHelpHubs from './components/InfoHelpHubs'
import InfoHelpLenta from './components/InfoHelpLenta'
import InfoHelpPosts from './components/InfoHelpPosts'

const logger = createLogger()
const store = createStore(reducer, composeWithDevTools(applyMiddleware(thunk, logger)))
const history = syncHistoryWithStore(hashHistory, store)

// TODO        #habracut  #comments,       (    URL)

// TODO react-router/examples/huge-apps - Partial App Loading

// TODO /help/... > page-header__title > ` -> ${title}`  

// TODO /topic/add/ > page-header__title > "  "

// TODO   url   /     nginx

// TODO ,   : `/#/post/${id}/` > `/#/company/${nick}/blog/${id}/`

render(
  <Provider store={store}>
    <Router history={history}>
      <Route path="/" component={App}>
        <IndexRoute component={PostTeaserList} />
        <Route path="flows" component={PostTeaserList}>
          <Route path=":selectedFlow" />
        </Route>
        <Route path="post" component={PostFull}>
          <Route path=":postId" />
        </Route>
        <Route path="topic/add" component={TopicAdd} title=" " />
        <Route path="feedback" component={Feedback} title=" " />
        <Redirect from="info" to="info/help/" />
        <Route path="info">
          <Route path="about" component={InfoAbout} title=" " />
          <Route path="help">
            <IndexRoute component={InfoHelp} title="" />
            <Route path="rules" component={InfoHelpRules} title="   " />
            <Route path="other" component={InfoHelpOther} title="  " />
            <Route path="registration" component={InfoHelpRegistration} title="  " />
            <Route path="habracentre" component={InfoHelpHabracentre} title="  " />
            <Route path="karma" component={InfoHelpKarma} title="  " />
            <Route path="settings" component={InfoHelpSettings} title="  " />
            <Route path="hubs" component={InfoHelpHubs} title="  " />
            <Route path="lenta" component={InfoHelpLenta} title="  " />
            <Route path="posts" component={InfoHelpPosts} title="  " />
          </Route>
        </Route>
      </Route>
      <Route path="*" component={NotFound} />
    </Router>
  </Provider>,
  document.getElementById('root')
)



